.avatar {
  height: 50px;
  width: 50px;
  max-width: 50px;
  min-height: 50px;
  margin: 10px;
}

.btn-secondary {
  background-color: transparent;
  border-style: none;
  outline: none;
}

.btn-secondary:hover {
  background-color: transparent;
}

.dtl-card {
  position: relative;
  text-align: start;
}

:root {
  --black: hsl(0, 0%, 0%);
  --dark-blue: rgb(22, 31, 47);
  --font-color-primary: white;
  --font-color-secondary: rgb(199, 199, 199);
  --search-input-bg: rgb(44, 52, 67);
  --search-input-font-color: rgb(142, 147, 154);
  --icon-btn: rgb(142, 147, 154);
  --icon-btn-hover: rgb(57, 66, 85);
  --icon-btn-active: rgb(43, 53, 75);
}

.icon-btn {
  border-radius: 100%;
  /* padding: 5px; */
  margin: 5px;
  height: 40px;
  width: 40px;
  outline: none;
  border-style: none;
  background: transparent;
  color: var(--icon-btn);
}

.icon-btn:hover {
  background-color: var(--icon-btn-hover);
}

.icon-btn:active {
  background-color: var(--icon-btn-active);
}

.search-input-container {
  color: var(--search-input-font-color);
  display: inherit;
  align-items: center;
  padding: 0px 15px 0px 10px;
  background-color: var(--search-input-bg);
  height: 40px;
  border-radius: 100px;
}

.search-input {
  background-color: var(--search-input-bg);
  color: var(--search-input-font-color);
  border-style: none;
  outline: none;
  height: 100%;
  width: 350px;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
}

@font-face {
  font-family: Roboto;
  src: url("/src/assets/fonts/roboto/Roboto-Regular.ttf");
  font-display: swap;
}
@font-face {
  font-family: RobotoBold;
  src: url("/src/assets/fonts/roboto/Roboto-Bold.ttf");
  font-display: swap;
}
h1 {
  font-family: RobotoBold;
}

h3 {
  color: var(--font-color-primary);
  font-weight: 500;
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: var(--font-color-primary);
}

a:hover {
  color: var(--font-color-secondary);
}

body {
  background-color: var(--black);
  font-family: Roboto, sans-serif;
  color: var(--font-color-primary);
}

img {
  display: block;
  height: 250px;
  margin: 5px;
  border-radius: 10px;
}

.bottom-left {
  position: absolute;
  bottom: 10%;
  left: 50px;
}

.col {
  margin: 0;
  padding: 0;
}

::-webkit-scrollbar {
  width: 10px;
  height: 7px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #9c9c9c;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #585858;
}

.logo-img {
  width: 70px;
}

.navbar-bg {
  background-color: var(--dark-blue);
}

.img-detailed {
  height: 500px !important;
  -webkit-mask-image: -webkit-gradient(linear, left top, left bottom, from(black), to(rgba(0, 0, 0, 0.3)));
}

.dropdown ul {
  background-color: white;
}

.hide {
  display: none;
}

.show {
  display: block;
}